<template>
	<div id="login">
		<div class="loginMain">
			<!-- <h2 class="loginMainTitle">Arxsys 远程会诊管理系统</h2> -->
			<div class="loginForm">
				<Form ref="loginForm" :model="loginForm" :rules="ruleInline">
					<!-- <FormItem prop="type">
						<RadioGroup v-model="loginForm.type" type="button">
							<Radio label="1" style="width: 100px">管理员账号</Radio>
							<Radio label="2" style="width: 100px">专家端账号</Radio>
							<Radio label="3" style="width: 100px">申请端账号</Radio>
						</RadioGroup>
					</FormItem> -->
					<FormItem prop="user">
						<Input prefix="md-person" placeholder="请输入管理员账号" v-model="loginForm.user" size="large" />
					</FormItem>
					<FormItem prop="password">
						<Input type="password" prefix="md-lock" placeholder="请输入密码" v-model="loginForm.password"
							size="large" />
					</FormItem>
					<FormItem prop="verify">
						<Input class="verifyEnter" type="text" prefix="md-apps" placeholder="请输入验证码"
							v-model="loginForm.verify" size="large" />
						<div class="verifyCode" id="v_container"></div>
					</FormItem>
					<FormItem>
						<Button type="primary" @click="loginSubmit('loginForm')" long size="large">登录</Button>
					</FormItem>
				</Form>
			</div>
		</div>
	</div>
</template>

<script>
	import {
		GVerify
	} from '../../libs/verifyCode';
	import {
		setToken
	} from '../../libs/util'
	export default {
		data() {
			return {
				loginForm: {
					user: '',
					password: '',
					verify: '',
					type: ''
				},
				ruleInline: {
					type: [{
						required: true,
						message: '请选择账号类型',
						trigger: 'change'
					}],
					user: [{
						required: true,
						message: '请输入管理员账号',
						trigger: 'blur'
					}],
					password: [{
						required: true,
						message: '请输入账号密码',
						trigger: 'blur'
					}],
					verify: [{
						required: true,
						message: '请输入图形验证码',
						trigger: 'blur'
					}]
				},
				verify: null
			}
		},
		methods: {
			loginSubmit(name) {
				var _this = this;
				_this.$refs[name].validate((valid) => {
					if (valid) {
						var verifyCode = _this.loginForm.verify;
						var verifyFlag = _this.verify.validate(verifyCode);
						if (!verifyFlag) {
							_this.$Message.error('验证码输入错误');
							setTimeout(() => {
								_this.verify.refresh()
								_this.loginForm.verify = '';
							}, 100);

						} else {
							if (_this.loginForm.type == '1') {
								let logindata = {
									adminnumber: _this.loginForm.user,
									adminpassword: _this.loginForm.password
								};
								_this.$post('/admin/logoAdmin', logindata)
									.then((res) => {
										if (res.code != '0000') {
											_this.$Message.error(res.message);
											_this.verify.refresh()
											_this.loginForm.verify = '';
										} else {
											setToken(res.data);
											_this.$Message.success('登录成功!');
											_this.$router.push('home')
										}
									})
							} else {

							}


						}
					} else {
						_this.$Message.error('必填不可为空');
						_this.loginForm.verify = '';
						_this.verify.refresh();
					}
				})
			}
		},
		mounted() {
			this.verify = new GVerify('v_container')
		}
	}
</script>

<style>
	body {
		width: 100%;
		height: 100%;
		background-image: url(../../assets/login.jpg);
		background-repeat: no-repeat;
		background-size: 100% 100vh;
	}

	.loginMain {
		width: 500px;
		height: 500px;
		background: rgba(255, 255, 255, 0.8);
		margin: 0 auto;
		position: relative;
		top: 50%;
		margin-top: 250px;

		padding: 20px;
		border-radius: 10px;
	}

	.loginMainTitle {
		margin-top: 40px;
		font-weight: lighter;
		font-size: 32px;
		text-align: center;
	}

	.loginForm {
		width: 300px;
		margin: 40px auto;
	}

	.verifyEnter {
		width: 200px;
		margin-right: 3px;

	}

	.verifyCode {
		width: calc(100% - 205px);
		background-color: #D7DDE4;
		height: 40px;
		display: inline-block;
		position: relative;
		vertical-align: middle;
		border-radius: 4px;
		overflow: hidden;
	}

	input::-webkit-input-placeholder {
		font-size: 14px;
	}

	select::-webkit-input-placeholder {
		font-size: 14px;
	}
</style>
